<template>
    <div id="new-recipe">
        <h4>New Recipe</h4>
        <div class="row">
            <form @submit.prevent="saveRecipe" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="recipe_id" required>
                        <label>Recipe ID</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                        <label>Name</label>
                    </div>
                </div>
                <div class="md-layout md-gutter">
                    <div class="md-layout-item">
                    <md-field>
                        <label>Ingredient 1</label>
                        <md-select v-model="ingredient1" required>  <!--name="style" id="style">-->
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option v-for="bottle in bottleDatabase" v-bind:key="bottle.id" class="collection-item" style="background-color: white;"
                            :value="bottle.name">{{bottle.name}}</md-option>
                        </md-select>
                    </md-field>
                    </div>
                    <div class="md-layout-item">
                    <md-field>
                        <label>Amount 1</label>
                        <md-select v-model="amount1" required>
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option style="background-color: white;" value="25 mL">25 mL</md-option>
                            <md-option style="background-color: white;" value="50 mL">50 mL</md-option>
                            <md-option style="background-color: white;" value="100 mL">100 mL</md-option>
                            <md-option style="background-color: white;" value="150 mL">150 mL</md-option>
                        </md-select>
                    </md-field>
                    </div>
                </div>
                <div class="md-layout md-gutter">
                    <div class="md-layout-item">
                    <md-field>
                        <label>Ingredient 2</label>
                        <md-select v-model="ingredient2">  <!--name="style" id="style">-->
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option v-for="bottle in bottleDatabase" v-bind:key="bottle.id" class="collection-item" style="background-color: white;"
                            :value="bottle.name">{{bottle.name}}</md-option>
                        </md-select>
                    </md-field>
                    </div>
                    <div class="md-layout-item">
                    <md-field>
                        <label>Amount 2</label>
                        <md-select v-model="amount2">
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option style="background-color: white;" value="25 mL">25 mL</md-option>
                            <md-option style="background-color: white;" value="50 mL">50 mL</md-option>
                            <md-option style="background-color: white;" value="100 mL">100 mL</md-option>
                            <md-option style="background-color: white;" value="150 mL">150 mL</md-option>
                        </md-select>
                    </md-field>
                    </div>
                </div>
                <div class="md-layout md-gutter">
                    <div class="md-layout-item">
                    <md-field>
                        <label>Ingredient 3</label>
                        <md-select v-model="ingredient3">  <!--name="style" id="style">-->
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option v-for="bottle in bottleDatabase" v-bind:key="bottle.id" class="collection-item" style="background-color: white;"
                            :value="bottle.name">{{bottle.name}}</md-option>
                        </md-select>
                    </md-field>
                    </div>
                    <div class="md-layout-item">
                    <md-field>
                        <label>Amount 3</label>
                        <md-select v-model="amount3">
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option style="background-color: white;" value="25 mL">25 mL</md-option>
                            <md-option style="background-color: white;" value="50 mL">50 mL</md-option>
                            <md-option style="background-color: white;" value="100 mL">100 mL</md-option>
                            <md-option style="background-color: white;" value="150 mL">150 mL</md-option>
                        </md-select>
                    </md-field>
                    </div>
                </div>
                <div class="md-layout md-gutter">
                    <div class="md-layout-item">
                    <md-field>
                        <label>Ingredient 4</label>
                        <md-select v-model="ingredient4">  <!--name="style" id="style">-->
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option v-for="bottle in bottleDatabase" v-bind:key="bottle.id" class="collection-item" style="background-color: white;"
                            :value="bottle.name">{{bottle.name}}</md-option>
                        </md-select>
                    </md-field>
                    </div>
                    <div class="md-layout-item">
                    <md-field>
                        <label>Amount 4</label>
                        <md-select v-model="amount4">
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option style="background-color: white;" value="25 mL">25 mL</md-option>
                            <md-option style="background-color: white;" value="50 mL">50 mL</md-option>
                            <md-option style="background-color: white;" value="100 mL">100 mL</md-option>
                            <md-option style="background-color: white;" value="150 mL">150 mL</md-option>
                        </md-select>
                    </md-field>
                    </div>
                </div>
                <div class="md-layout md-gutter">
                    <div class="md-layout-item">
                    <md-field>
                        <label>Ingredient 5</label>
                        <md-select v-model="ingredient5">  <!--name="style" id="style">-->
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option v-for="bottle in bottleDatabase" v-bind:key="bottle.id" class="collection-item" style="background-color: white;"
                            :value="bottle.name">{{bottle.name}}</md-option>
                        </md-select>
                    </md-field>
                    </div>
                    <div class="md-layout-item">
                    <md-field>
                        <label>Amount 5</label>
                        <md-select v-model="amount5">
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option style="background-color: white;" value="25 mL">25 mL</md-option>
                            <md-option style="background-color: white;" value="50 mL">50 mL</md-option>
                            <md-option style="background-color: white;" value="100 mL">100 mL</md-option>
                            <md-option style="background-color: white;" value="150 mL">150 mL</md-option>
                        </md-select>
                    </md-field>
                    </div>
                </div>
                <router-link to="/recipes" class="btn grey lighten-2 black-text" style="margin-right: 10px; text-decoration: none">Cancel</router-link>
                <button type="submit" class="btn orange">Submit</button>
                <br><br>
            </form>
        </div>
    </div>
</template>
<script>
import Vue from 'vue'
import VueMaterial from 'vue-material'
import 'vue-material/dist/vue-material.min.css'

Vue.use(VueMaterial)
import db from './firebaseInit'
export default {
    name: 'new-recipe',
    data () {
        return {
            recipe_id: null,
            name: null,
            ingredient1: null,
            amount1: null,
            ingredient2: null,
            amount2: null,
            ingredient3: null,
            amount3: null,
            ingredient4: null,
            amount4: null,
            ingredient5: null,
            amount5: null,
            bottleDatabase: []
        }
    },
    created () {
        db.collection('bottleDatabase').orderBy('name').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'name': doc.data().name,
                }
                this.bottleDatabase.push(data)
            })
        })
    },
    methods: {
        saveRecipe () {
            db.collection('recipes').add({
                recipe_id: this.recipe_id,
                name: this.name,
                ingredient1: this.ingredient1,
                amount1: this.amount1,
                ingredient2: this.ingredient2,
                amount2: this.amount2,
                ingredient3: this.ingredient3,
                amount3: this.amount3,
                ingredient4: this.ingredient4,
                amount4: this.amount4,
                ingredient5: this.ingredient5,
                amount5: this.amount5
            })
            .then(docRef => this.$router.push('/recipes'))
            .catch(error => console.log(err))
        }
    }
}
</script>


