<template>
    <div id="bottles">
        <br>
        <ul class="collection with-header">
            <li class="collection-header"><h4>Bottles</h4><li/>
            <li v-for="bottle in bottles" v-bind:key="bottle.id" class="collection-item">
                <div class="chip">{{bottle.pos}}</div>  {{bottle.name}}

                <router-link class="secondary-content" v-bind:to="{name: 'edit-bottles', params: {pos: bottle.pos}}">
                    <i class="fa fa-pencil" style="font-size:30px;color:red;margin-right:10px;margin-top:2px"></i>
                </router-link>
    
            </li>
            </ul>
        <router-link to="/editor" class="btn grey lighten-2 black-text" style="text-decoration: none">Return</router-link>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'bottles',
    data () {
        return {
            bottles: []
        }
    },
    created () {
        db.collection('bottles').orderBy('pos').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'pos': doc.data().pos,
                    'name': doc.data().name,
                }
                this.bottles.push(data)
            })
        })

    }
}
</script>


