<template>
    <div>
        <div class="container">
            <div class="row">
                <div class="col s12 m8 offset m2">
                    <br>
                    <div class="login card-panel red white-text center">
                        <h3>Login</h3>
                        <form>
                            <div class="input-field">
                                <i class="material-icons prefix">email</i>
                                <input class="white-text" type="text" id="email" v-model="email">
                                <label class="white-text" for="email">Email</label>
                            </div>
                            <div class="input-field">
                                <i class="material-icons prefix">lock</i>
                                <input class="white-text" type="password" id="password" v-model="password">
                                <label class="white-text" for="password">Password</label>
                            </div>
                            <button v-on:click="login" class="btn btn-large grey lighten-3 black-text">Login</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase'
export default {
    name: 'login',
    data: function() {
        return {
            email: '',
            password: ''
        }
    },
    methods: {
        login: function(e) {
            firebase.auth().signInWithEmailAndPassword(this.email, this.password)
            .then(user => {
                //location.reload()
                alert('You have successfully logged in.')
                this.$router.go({path: this.$router.path})
                location.reload()
            },
            err => {
                alert(err.message)
            })
            e.preventDefault()
        }
    }
}
</script>



