<template>
    <div id="edit-bottles">
        <h4>Edit Bottle</h4>
        <div class="row">
            <form @submit.prevent="updateBottles" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="pos" required>
                    </div>
                </div>
                <!--<div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                    </div>
                </div>-->
                <div class="md-layout md-gutter">
                    <div class="md-layout-item">
                    <md-field>
                        <md-select v-model="name">  <!--name="style" id="style">-->
                            <md-option style="background-color: white;" value="No Bottle"><em>No Bottle</em></md-option>
                            <md-option v-for="bottle in bottleDatabase" v-bind:key="bottle.id" class="collection-item" style="background-color: white;"
                            :value="bottle.name">{{bottle.name}}</md-option>
                        </md-select>
                    </md-field>
                    </div>
                </div>
                <br><br>
                <router-link to="/bottles"
                class="btn grey lighten-2 black-text" style="margin-right:10px; text-decoration: none">Cancel</router-link>
                <button type="submit" class="btn yellow black-text">Submit</button>
                <router-link to="/new-bottle" class="btn orange" style="margin-left: 10px; text-decoration: none">Add New</router-link>
            </form>
        </div>
    </div>
</template>
<script>
import Vue from 'vue'
import VueMaterial from 'vue-material'
import 'vue-material/dist/vue-material.min.css'

Vue.use(VueMaterial)
import db from './firebaseInit'
//import dropdown from 'vue-dropdowns'
export default {
    name: 'edit-bottles',
    data() {
            return {
                name: null,
                pos: null,
                bottleDatabase: []
            }
        },
    created () {
        db.collection('bottleDatabase').orderBy('name').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'name': doc.data().name,
                }
                this.bottleDatabase.push(data)
            })
        })

    },
    beforeRouteEnter(to, from, next) {
        db.collection('bottles').where('pos',
        '==', to.params.pos).get()
            .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.pos = doc.data().pos
                    vm.name = doc.data().name
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('bottles').where
            ('pos', '==', this.$route.params.pos).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.pos = doc.data().pos
                    this.name = doc.data().name
                })
            })
        },
        updateBottles () {
            db.collection('bottles').where
            ('pos', '==', this.$route.params.pos).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        pos: this.pos,
                        name: this.name
                    })
                    .then(() => {
                        this.$router.push({name: 'bottles',
                        params: {pos: this.pos}})
                    })
                })
            })
        }
    }
}
</script>


