<template>
    <div id="view-recipe">
        <br>
        <ul class="collection with-header">
            <li class="collection-header"><h4>{{name}}</h4></li>
            <li class="collection-item">Recipe ID: {{recipe_id}}</li>
            <li class="collection-item">Ingredient 1: {{ingredient1}}, {{amount1}}</li>
            <li class="collection-item" v-if="ingredient2">Ingredient 2: {{ingredient2}}, {{amount2}}</li>
            <li class="collection-item" v-if="ingredient3">Ingredient 3: {{ingredient3}}, {{amount3}}</li>
            <li class="collection-item" v-if="ingredient4">Ingredient 4: {{ingredient4}}, {{amount4}}</li>
            <li class="collection-item" v-if="ingredient5">Ingredient 5: {{ingredient5}}, {{amount5}}</li>
        </ul>
        <router-link to="/recipes" class="btn grey lighten-2 black-text" style="margin-right:10px; text-decoration: none">Return</router-link>
        <router-link v-bind:to="{name: 'edit-recipe',
        params: {recipe_id: recipe_id}}"
        class="btn yellow black-text" style="margin-right:10px; text-decoration: none">Edit</router-link>
        <button @click="deleteRecipe" class="btn red">Delete</button>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-recipe',
    data () {
        return {
            recipe_id: null,
            name: null,
            ingredient1: null,
            amount1: null,
            ingredient2: null,
            amount2: null,
            ingredient3: null,
            amount3: null,
            ingredient4: null,
            amount4: null,
            ingredient5: null,
            amount5: null
        }
    },
    beforeRouteEnter(to, from, next) {
        db.collection('recipes').where('recipe_id',
        '==', to.params.recipe_id).get()
            .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.recipe_id = doc.data().recipe_id
                    vm.name = doc.data().name
                    vm.ingredient1 = doc.data().ingredient1
                    vm.amount1 = doc.data().amount1
                    vm.ingredient2 = doc.data().ingredient2
                    vm.amount2 = doc.data().amount2
                    vm.ingredient3 = doc.data().ingredient3
                    vm.amount3 = doc.data().amount3
                    vm.ingredient4 = doc.data().ingredient4
                    vm.amount4 = doc.data().amount4
                    vm.ingredient5 = doc.data().ingredient5
                    vm.amount5 = doc.data().amount5
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('recipes').where
            ('recipe_id', '==', this.$route.params.recipe_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.recipe_id = doc.data().recipe_id
                    this.name = doc.data().name
                    this.ingredient1 = doc.data().ingredient1
                    this.amount1 = doc.data().amount1
                    this.ingredient2 = doc.data().ingredient2
                    this.amount2 = doc.data().amount2
                    this.ingredient3 = doc.data().ingredient3
                    this.amount3 = doc.data().amount3
                    this.ingredient4 = doc.data().ingredient4
                    this.amount4 = doc.data().amount4
                    this.ingredient5 = doc.data().ingredient5
                    this.amount5 = doc.data().amount5
                })
            })
        },
        deleteRecipe () {
            if(confirm('Are you sure?')) {
                db.collection('recipes').where('recipe_id', '==',
                this.$route.params.recipe_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete()
                        this.$router.push('/recipes')
                    })
                })
            }
        }
    }
}
</script>


