<template>
    <div id="new-bottle">
        <h4>New Bottle</h4>
        <div class="row">
            <form @submit.prevent="saveBottle" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                        <label>Name</label>
                    </div>
                </div>
                <router-link to="/bottles" class="btn grey lighten-2 black-text" style="margin-right:10px; text-decoration: none">Cancel</router-link>
                <button type="submit" class="btn orange">Submit</button>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'new-bottle',
    data () {
        return {
            name: null
        }
    },
    methods: {
        saveBottle () {
            db.collection('bottleDatabase').add({
                name: this.name
            })
            .then(docRef => this.$router.push('/bottles'))
            //.then(() => {
                alert('Your bottle has been added.')
            //    this.$router.go({path: this.$router.path})
            //.catch(error => console.log(err))
        }
    }
}
</script>


