import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import Editor from '@/components/Editor'
import Recipes from '@/components/Recipes'
import Bottles from '@/components/Bottles'
import NewRecipe from '@/components/NewRecipe'
import NewBottle from '@/components/NewBottle'
import ViewRecipe from '@/components/ViewRecipe'
import EditRecipe from '@/components/EditRecipe'
import EditBottles from '@/components/EditBottles'
import Login from '@/components/Login'
import Register from '@/components/Register'
import firebase from 'firebase'

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/editor',
      name: 'editor',
      component: Editor,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/recipes',
      name: 'recipes',
      component: Recipes,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/bottles',
      name: 'bottles',
      component: Bottles,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/new-recipe',
      name: 'new-recipe',
      component: NewRecipe,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/new-bottle',
      name: 'new-bottle',
      component: NewBottle,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/edit-recipe/:recipe_id',
      name: 'edit-recipe',
      component: EditRecipe,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/edit-bottles/:pos',
      name: 'edit-bottles',
      component: EditBottles,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/:recipe_id',
      name: 'view-recipe',
      component: ViewRecipe,
      meta: {
        requiresAuth: true
      }
    }
  ]
})

// Nav Guards
router.beforeEach((to, from, next) => {
  // Check from requiredAuth guard
  if(to.matched.some(record => record.meta.requiresAuth)) {
    // Check if NOT logged in
    if(!firebase.auth().currentUser) {
      //Go to login
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    }
    else {
      // Proceed to route
      next()
    }
  }
  else if(to.matched.some(record => record.meta.requiresGuest)) {
    // Check if logged in
    if(firebase.auth().currentUser) {
      //Go to login
      next({
        path: '/',
        query: {
          redirect: to.fullPath
        }
      })
    }
    else {
      // Proceed to route
      next()
    }
  }
  else {
    // Proceed to route
    next()
  }
})

export default router


