<template>
    <div id="edit-recipe">
        <h4>Edit Recipe</h4>
        <div class="row">
            <form @submit.prevent="updateRecipe" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="recipe_id" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                    </div>
                </div>
                <div class="md-layout md-gutter">
                    <div class="md-layout-item">
                    <md-field>
                        <label>Ingredient 1</label>
                        <md-select v-model="ingredient1" required>  <!--name="style" id="style">-->
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option v-for="bottle in bottleDatabase" v-bind:key="bottle.id" class="collection-item" style="background-color: white;"
                            :value="bottle.name">{{bottle.name}}</md-option>
                        </md-select>
                    </md-field>
                    </div>
                    <div class="md-layout-item">
                    <md-field>
                        <label>Amount 1</label>
                        <md-select v-model="amount1" required>
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option style="background-color: white;" value="25 mL">25 mL</md-option>
                            <md-option style="background-color: white;" value="50 mL">50 mL</md-option>
                            <md-option style="background-color: white;" value="100 mL">100 mL</md-option>
                            <md-option style="background-color: white;" value="150 mL">150 mL</md-option>
                        </md-select>
                    </md-field>
                    </div>
                </div>
                <div class="md-layout md-gutter">
                    <div class="md-layout-item">
                    <md-field>
                        <label>Ingredient 2</label>
                        <md-select v-model="ingredient2">  <!--name="style" id="style">-->
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option v-for="bottle in bottleDatabase" v-bind:key="bottle.id" class="collection-item" style="background-color: white;"
                            :value="bottle.name">{{bottle.name}}</md-option>
                        </md-select>
                    </md-field>
                    </div>
                    <div class="md-layout-item">
                    <md-field>
                        <label>Amount 2</label>
                        <md-select v-model="amount2">
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option style="background-color: white;" value="25 mL">25 mL</md-option>
                            <md-option style="background-color: white;" value="50 mL">50 mL</md-option>
                            <md-option style="background-color: white;" value="100 mL">100 mL</md-option>
                            <md-option style="background-color: white;" value="150 mL">150 mL</md-option>
                        </md-select>
                    </md-field>
                    </div>
                </div>
                <div class="md-layout md-gutter">
                    <div class="md-layout-item">
                    <md-field>
                        <label>Ingredient 3</label>
                        <md-select v-model="ingredient3">  <!--name="style" id="style">-->
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option v-for="bottle in bottleDatabase" v-bind:key="bottle.id" class="collection-item" style="background-color: white;"
                            :value="bottle.name">{{bottle.name}}</md-option>
                        </md-select>
                    </md-field>
                    </div>
                    <div class="md-layout-item">
                    <md-field>
                        <label>Amount 3</label>
                        <md-select v-model="amount3">
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option style="background-color: white;" value="25 mL">25 mL</md-option>
                            <md-option style="background-color: white;" value="50 mL">50 mL</md-option>
                            <md-option style="background-color: white;" value="100 mL">100 mL</md-option>
                            <md-option style="background-color: white;" value="150 mL">150 mL</md-option>
                        </md-select>
                    </md-field>
                    </div>
                </div>
                <div class="md-layout md-gutter">
                    <div class="md-layout-item">
                    <md-field>
                        <label>Ingredient 4</label>
                        <md-select v-model="ingredient4">  <!--name="style" id="style">-->
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option v-for="bottle in bottleDatabase" v-bind:key="bottle.id" class="collection-item" style="background-color: white;"
                            :value="bottle.name">{{bottle.name}}</md-option>
                        </md-select>
                    </md-field>
                    </div>
                    <div class="md-layout-item">
                    <md-field>
                        <label>Amount 4</label>
                        <md-select v-model="amount4">
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option style="background-color: white;" value="25 mL">25 mL</md-option>
                            <md-option style="background-color: white;" value="50 mL">50 mL</md-option>
                            <md-option style="background-color: white;" value="100 mL">100 mL</md-option>
                            <md-option style="background-color: white;" value="150 mL">150 mL</md-option>
                        </md-select>
                    </md-field>
                    </div>
                </div>
                <div class="md-layout md-gutter">
                    <div class="md-layout-item">
                    <md-field>
                        <label>Ingredient 5</label>
                        <md-select v-model="ingredient5">  <!--name="style" id="style">-->
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option v-for="bottle in bottleDatabase" v-bind:key="bottle.id" class="collection-item" style="background-color: white;"
                            :value="bottle.name">{{bottle.name}}</md-option>
                        </md-select>
                    </md-field>
                    </div>
                    <div class="md-layout-item">
                    <md-field>
                        <label>Amount 5</label>
                        <md-select v-model="amount5">
                            <md-option style="background-color: white;"><em>None</em></md-option>
                            <md-option style="background-color: white;" value="25 mL">25 mL</md-option>
                            <md-option style="background-color: white;" value="50 mL">50 mL</md-option>
                            <md-option style="background-color: white;" value="100 mL">100 mL</md-option>
                            <md-option style="background-color: white;" value="150 mL">150 mL</md-option>
                        </md-select>
                    </md-field>
                    </div>
                </div>
                <br>
                <router-link v-bind:to="{name: 'view-recipe',
                params: {recipe_id: recipe_id}}"
                class="btn grey lighten-2 black-text" style="margin-right:10px; text-decoration: none">Cancel</router-link>
                <button type="submit" class="btn orange">Submit</button>
                <br><br>
            </form>
        </div>
    </div>
</template>
<script>
import Vue from 'vue'
import VueMaterial from 'vue-material'
import 'vue-material/dist/vue-material.min.css'

Vue.use(VueMaterial)
import db from './firebaseInit'
import dropdown from 'vue-dropdowns'
export default {
    name: 'edit-recipe',
    data () {
        return {
            recipe_id: null,
            name: null,
            ingredient1: false,
            amount1: false,
            ingredient2: false,
            amount2: false,
            ingredient3: false,
            amount3: false,
            ingredient4: false,
            amount4: false,
            ingredient5: false,
            amount5: false,
            bottleDatabase: []
        }
    },
    created () {
        db.collection('bottleDatabase').orderBy('name').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'name': doc.data().name,
                }
                this.bottleDatabase.push(data)
            })
        })
    },
    beforeRouteEnter(to, from, next) {
        db.collection('recipes').where('recipe_id',
        '==', to.params.recipe_id).get()
            .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.recipe_id = doc.data().recipe_id
                    vm.name = doc.data().name
                    vm.ingredient1 = doc.data().ingredient1
                    vm.amount1 = doc.data().amount1
                    vm.ingredient2 = doc.data().ingredient2
                    vm.amount2 = doc.data().amount2
                    vm.ingredient3 = doc.data().ingredient3
                    vm.amount3 = doc.data().amount3
                    vm.ingredient4 = doc.data().ingredient4
                    vm.amount4 = doc.data().amount4
                    vm.ingredient5 = doc.data().ingredient5
                    vm.amount5 = doc.data().amount5
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('recipes').where
            ('recipe_id', '==', this.$route.params.recipe_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.recipe_id = doc.data().recipe_id
                    this.name = doc.data().name
                    this.ingredient1 = doc.data().ingredient1
                    this.amount1 = doc.data().amount1
                    this.ingredient2 = doc.data().ingredient2
                    this.amount2 = doc.data().amount2
                    this.ingredient3 = doc.data().ingredient3
                    this.amount3 = doc.data().amount3
                    this.ingredient4 = doc.data().ingredient4
                    this.amount4 = doc.data().amount4
                    this.ingredient5 = doc.data().ingredient5
                    this.amount5 = doc.data().amount5
                })
            })
        },
        updateRecipe () {
            db.collection('recipes').where
            ('recipe_id', '==', this.$route.params.recipe_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        recipe_id: this.recipe_id,
                        name: this.name,
                        ingredient1: this.ingredient1,
                        amount1: this.amount1,
                        ingredient2: this.ingredient2,
                        amount2: this.amount2,
                        ingredient3: this.ingredient3,
                        amount3: this.amount3,
                        ingredient4: this.ingredient4,
                        amount4: this.amount4,
                        ingredient5: this.ingredient5,
                        amount5: this.amount5
                    })
                    .then(() => {
                        this.$router.push({name: 'view-recipe',
                        params: {recipe_id: this.recipe_id}})
                    })
                })
            })
        }
    }
}
</script>



