<template>
    <div id="recipes">
        <br>
        <ul class="collection with-header">
            <li class="collection-header"><h4>Recipes</h4><li/>
            <li v-for="recipe in recipes" v-bind:key="recipe.id" class="collection-item">
                <div class="none"></div>{{recipe.name}}

                <router-link class="secondary-content" v-bind:to="{name: 'view-recipe', params: {recipe_id: recipe.recipe_id}}">
                    <i class="fa fa-eye" style="font-size:25px;color:red;margin-right:10px;margin-top:-1px"></i>
                    <!--<i class="fa fa-trash" style="font-size:30px;color:gray;"></i>-->
                </router-link>
    
            </li>
            </ul>
        <router-link to="/editor" class="btn grey lighten-2 black-text" style="text-decoration: none">Return</router-link>
        <router-link to="/new-recipe" class="btn orange" style="margin-left: 10px; text-decoration: none">Add New</router-link>
        <br><br>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'recipes',
    data () {
        return {
            recipes: []
        }
    },
    created () {
        db.collection('recipes').orderBy('name').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'recipe_id': doc.data().recipe_id,
                    'name': doc.data().name
                }
                this.recipes.push(data)
            })
        })

    }
}
</script>


